//Появляющееся слева меню
$.fn.sideMenu = function(logicShowMenu = () => {}, logicHideMenu = () => {}) {
	const self = $(this);

	"use strict";
	//Создаем бг
	const sideMenuBg = document.createElement(`div`);

	document.getElementsByTagName(`body`)[0]
			.appendChild(
					sideMenuBg
			);
	sideMenuBg.setAttribute(`id`, `side-menu-bg`);
	sideMenuBg.className = `side-menu-bg hide`;

	//При клике на бг - он исчезает
	sideMenuBg.addEventListener(`click`, () => {
		"use strict";
		sideMenuBg.className += ` hide`;
		logicHideMenu();
	});

	self[0].addEventListener(`click`, () => {
		"use strict";
		sideMenuBg.className = `side-menu-bg`;
		logicShowMenu();
	});

	return self;

};
//Появляющееся слева меню
const navMenuCollapse = document.getElementById(`nav-menu-collapse`);

$(`#navbar-burger`).sideMenu(() => {
	"use strict";
	//Логика при появлении черной подложки
	navMenuCollapse.className += ` nav-menu-collapse--show`;
}, () => {
	"use strict";
	//Логика при исчезновении черной подложки
	navMenuCollapse.className = `nav-menu-collapse`;
});

